input,select,textarea{appearance:none;border:none;outline:none;border-bottom:.2em solid #112a4d;background:rgba(17,42,77,.2);border-radius:.2em .2em 0 0;padding:.4em 1em;color:#f7f3fc;font-size:large}input::placeholder,select::placeholder,textarea::placeholder{color:rgba(12,31,56,.5);font-weight:500}input,textarea,.select-wrapper{width:auto;min-width:40%;max-width:100%;color:#f7f3fc}select{padding-right:2em}.select-wrapper .select-arrow{position:absolute;width:0;height:0;color:#f7f3fc}.select-wrapper .select-arrow::after{content:"â–¼";position:absolute;right:.5em;top:.3em;font-size:.8em;color:rgba(12,31,56,.5)}.select-wrapper .select-arrow:focus::after{transform:rotate(180deg)}button{background-image:none;border-radius:4px;box-sizing:border-box;cursor:pointer;display:inline-block;font-size:14px;font-weight:400;line-height:20px;margin:0 5px 10px 0;overflow:visible;padding:12px 40px;text-align:center;text-transform:none;touch-action:manipulation;user-select:none;-webkit-user-select:none;vertical-align:middle;white-space:nowrap;background-size:200% 100%;background-position:100% 0;transition:all .2s ease-in-out;background-image:linear-gradient(to left, rgb(35, 88, 161), rgb(12, 31, 56));background-color:#112a4d;border:1px solid #112a4d;box-shadow:#0c1f38 4px 4px 0 0,#112a4d 4px 4px 0 1px;color:#f7f3fc;text-shadow:0 5px 5px rgba(35,88,161,.4)}button:hover{background-position:0 0;transition:all .2s ease-in-out}button.secondary{background-image:linear-gradient(to left, rgb(55, 125, 55), rgb(8, 19, 8));background-color:#183618;border:1px solid #183618;box-shadow:#081308 4px 4px 0 0,#183618 4px 4px 0 1px;color:#f7f3fc;text-shadow:0 5px 5px rgba(55,125,55,.4)}button.object{border:1px solid #fc0;background:linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, rgba(255, 224, 102, 0.4) 50%, rgba(0, 0, 0, 0.4) 100%) #ffe066;box-shadow:0px 0px 0px 1px rgba(0,0,0,.25) inset,0px 0px 1px 2px rgba(255,255,255,.25) inset,0px -5px 50px -15px #665200 inset,0px 0px 4px rgba(0,0,0,.2),0px 1px 2px rgba(0,0,0,.5);text-shadow:0 5px 5px rgba(255,224,102,.4);color:#1a1919}button.object-dark{border:1px solid #665200;background:linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, rgba(102, 82, 0, 0.4) 50%, rgba(0, 0, 0, 0.4) 100%) #665200;box-shadow:0px 0px 0px 1px rgba(0,0,0,.25) inset,0px 0px 1px 2px rgba(255,255,255,.25) inset,0px -5px 50px -15px #ffe066 inset,0px 0px 4px rgba(0,0,0,.2),0px 1px 2px rgba(0,0,0,.5);text-shadow:0 5px 5px rgba(102,82,0,.4);color:#f7f3fc}button:disabled{background-image:none;background-color:#676170;border:1px solid #676170;box-shadow:#353239 4px 4px 0 0;color:#1a1919;transition:all .2s ease-in-out}